.in__usa {
  padding: 40px;
}

/*  */

.list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 1px solid white;
  border-radius: 16px;
  width: fit-content;
  padding: 8px 12px 8px 12px;
  box-shadow: 0px 4px 26px 0px rgb(248, 179, 252);
  background: linear-gradient(
    0deg,
    rgba(0, 255, 184, 0.4) -10%,
    rgba(9, 9, 121, 0) 22%,
    rgba(145, 6, 163, 0.3) 50%,
    rgba(4, 124, 197, 0) 77%,
    rgba(0, 212, 255, 0.4) 110%
  );
}

.item {
  width: calc(100vw - 130px);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 6px;
}

.avatar {
  padding: 4px;
  width: 88px;
  border: 1px solid rgba(255, 255, 255, 0.547);
  border-radius: 50%;
  box-shadow: inset 0px 0px 14px 0px rgb(255, 255, 255);
}

.name {
  text-transform: uppercase;
  font-weight: 800;
  font-size: 24px;
}

@media screen and (min-width: 800px) {
  .in__usa {
    padding: 0;
  }

  .list {
    height: 400px;
    justify-content: space-around;
    transform: scale(0.9);
    transition: all 250ms;
  }

  .list:hover {
    cursor: pointer;
    transform: scale(1.1);
  }

  .item {
    width: 400px;
    /* width: calc((100vw / 3) - 100px); */
  }

  .avatar {
    width: 130px;
  }

  .name {
    font-size: 30px;
  }
}
